* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 15px;
}

img {
  width: 50rem;
  height: 40rem;
}

.portrait {
  max-width: 350px;
  max-height: 250px;
  float: left;
}

.nav {
  width: 100%;
  margin: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  background-color: rgb(243, 228, 227);
  padding-bottom: 5px;
}

.nav h1 {
  font-size: 5rem;
  text-align: center;
  margin-top: 10px;
}

.nav h2 {
  font-size: 3rem;
  text-align: center;
}

.links {
  margin-right: 10px;
}

main {
  position: relative;
  top: 14rem;
}

a {
  text-decoration: none;
  color: black;
  font-size: 2rem;
  font-weight: 100;
}

a:hover {
  color: #f26d9e;
}

p {
  font-size: 1rem;
  padding: 1rem 2rem;
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}

.navBottom {
  display: flex;
  justify-content: space-between;
  margin: 2rem 2rem 0 2rem;
}

.icon {
  height: 1.5rem;
  width: 1.5rem;
  margin: 0 0.5rem;
}

.icon:hover {
  height: 2rem;
  width: 2rem;
}

.projectLinks {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.projectContainer {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.imageAndHover {
  position: relative;
}

.projectImage {
  border-radius: 1rem;
}

.hoverDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(240, 233, 233);
  width: 50rem;
  height: 40rem;
  border-radius: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  padding-left: 2rem;
  padding-right: 2rem;
}

.hoverDiv p {
  font-size: 1.5rem;
  color: black;
  font-weight: bold;
}

.hoverDiv:hover {
  opacity: 0.9;
}

.about {
  width: 80%;
  margin: 2rem auto;
}

.about p {
  font-size: 1.5rem;
  font-family: "Caveat", cursive;
  word-wrap: break-word;
}

.projectsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.welcomeBanner {
  background-color: rgba(254, 201, 210, 0.8);
  margin-top: 0;
  padding: 4rem 2rem;
}

.welcomeBanner p {
  font-size: 2rem;
  font-family: "Caveat", cursive;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 600px) {
  img,
  .hoverDiv {
    width: 20rem;
    height: 15rem;
  }

  .hoverDiv p {
    font-size: 1rem;
  }

  .nav h1 {
    font-size: 3rem;
  }

  .nav h2 {
    font-size: 1.5rem;
  }

  a {
    font-size: 1.5rem;
  }

  .navBottom {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 7rem;
  }
}

@media only screen and (max-width: 400px) {
  .navBottom {
    margin-top: -0.5rem;
  }
}
